.App {
  text-align: center;
}

.border-radius{
  /* border: 2px solid red; */
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.row{
  justify-content: center;
  align-items: center;
}

.col-sm{
  position: relative;
  width: 150px;
  height: 180px;
  margin: 10px 15px;
  padding: 0px;
  border: 2px solid rgb(146, 145, 145);
  border-radius: 14px;
  outline: none;
  box-sizing: border-box;
  cursor: pointer;
}

.col-sm img{
  width: 100%;
  height: 150px;

}

h4{
  font-size: 1.2rem;
  display: block;
}

.footer{
  position: absolute;
  height: 50px;
  width: 100%;
  margin-top: 30px;
  margin-bottom: 0rem;
}

.footer p{
  display: flex;
  justify-content: center;
  margin: 10px auto;
  
}